    box-sizing: border-box;
}
.heading {
    font-family: "Roboto";
    letter-spacing: 2px;
    font-size: 48px;
    font-weight: 900;
    color: rgba(145, 145, 145, 0.993);
    font-style: normal;
}
body{
    font-family: "Roboto";
    background-color: aquamarine;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
form{
    padding-top: 40px;
    display: flex;
    flex-direction: column;
    height: 200px;
    align-items: center;
}
.search-box{
    display: flex;
    width: 532px;
    justify-content: center;
    border-radius: 32px;
    overflow: hidden;
}
.search-box input{
    width: 500px;
    height: 28px;
    outline: none;
    border: none;
    padding: 20px;
    font-size: 20px;
    background-color: rgba(255, 255, 255, 0.5);
}

.search-box input::placeholder{
    color: rgba(0, 0, 0, 0.701);
}
form label{
    font-size: 24px;
}
.search-box button{
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.5);
    border: none;
}

.fluent--search-12-filled {
  display: inline-block;
  width: 20px;
  height: 20px;
  --svg: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-color: currentColor;
  -webkit-mask-image: var(--svg);
  mask-image: var(--svg);
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-size: 100% 100%;
  mask-size: 100% 100%;
  color: rgba(0, 0, 0, 0.701);
}

.content{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


/* weather details */
.card{
    border-radius: 20px;
    height: 440px;
    width: 400px;
    background-color: rgba(197, 197, 197, 0.607);
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#place-holder{
    font-weight: 900;
    font-size: 32px;
}
img{
    border-radius: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    height: 200px;
    width: 360px;
}
.weather-details{
    text-align: center;
}
#condition-holder{
    font-weight: 600;
    font-size: 20px;
}
#temp-holder{
    padding: 10px;
    font-weight: 900;
    font-size: 32px;
}

/* loading icon */
.eos-icons--loading {
  color: rgb(227, 254, 186);
  margin: 20px;
  padding: 28px;  
  display: inline-block;
  width: 24px;
  height: 24px;
  --svg: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-color: currentColor;
  -webkit-mask-image: var(--svg);
  mask-image: var(--svg);
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-size: 100% 100%;
  mask-size: 100% 100%;
}

body{
    background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
}`, "",{"version":3,"sources":["webpack://./src/style/style.css"],"names":[],"mappings":"AAEA;IACI,sBAAsB;AAC1B;AACA;IACI,qBAAqB;IACrB,mBAAmB;IACnB,eAAe;IACf,gBAAgB;IAChB,iCAAiC;IACjC,kBAAkB;AACtB;AACA;IACI,qBAAqB;IACrB,4BAA4B;IAC5B,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;AACA;IACI,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;IACnB,gBAAgB;AACpB;AACA;IACI,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,aAAa;IACb,eAAe;IACf,0CAA0C;AAC9C;;AAEA;IACI,2BAA2B;AAC/B;AACA;IACI,eAAe;AACnB;AACA;IACI,WAAW;IACX,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,0CAA0C;IAC1C,YAAY;AAChB;;AAEA;EACE,qBAAqB;EACrB,WAAW;EACX,YAAY;EACZ,8CAAuQ;EACvQ,8BAA8B;EAC9B,8BAA8B;EAC9B,sBAAsB;EACtB,8BAA8B;EAC9B,sBAAsB;EACtB,4BAA4B;EAC5B,oBAAoB;EACpB,2BAA2B;AAC7B;;AAEA;IACI,aAAa;IACb,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;;;AAGA,oBAAoB;AACpB;IACI,mBAAmB;IACnB,aAAa;IACb,YAAY;IACZ,4CAA4C;IAC5C,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;AACA;IACI,gBAAgB;IAChB,eAAe;AACnB;AACA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,mBAAmB;IACnB,aAAa;IACb,YAAY;AAChB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,gBAAgB;IAChB,eAAe;AACnB;AACA;IACI,aAAa;IACb,gBAAgB;IAChB,eAAe;AACnB;;AAEA,iBAAiB;AACjB;EACE,yBAAyB;EACzB,YAAY;EACZ,aAAa;EACb,qBAAqB;EACrB,WAAW;EACX,YAAY;EACZ,8CAAub;EACvb,8BAA8B;EAC9B,8BAA8B;EAC9B,sBAAsB;EACtB,8BAA8B;EAC9B,sBAAsB;EACtB,4BAA4B;EAC5B,oBAAoB;AACtB;;AAEA;IACI,mDAAoD;IACpD,4BAA4B;IAC5B,4BAA4B;IAC5B,2BAA2B;IAC3B,sBAAsB;AAC1B","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');\n\n*{\n    box-sizing: border-box;\n}\n.heading {\n    font-family: \"Roboto\";\n    letter-spacing: 2px;\n    font-size: 48px;\n    font-weight: 900;\n    color: rgba(145, 145, 145, 0.993);\n    font-style: normal;\n}\nbody{\n    font-family: \"Roboto\";\n    background-color: aquamarine;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    flex-direction: column;\n}\nform{\n    padding-t